import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import HomePage from './components/Home.vue'
import LoginPage from './components/Login.vue'

export default new Router({
    routes: [
        {
            path: '/',
            name: 'LoginPage',
            component: LoginPage
        },
        {
            path: '/home',
            name: 'HomePage',
            component: HomePage
        }
    ]
})
